.top-form {
	position: relative;
	width: 100%;
	padding: 1px 0;
	margin-bottom: 33px;
	background: url(../img/form_bg.jpg), url(../img/layer-2.png);
	@include cover;
	&:before {
		content: "";
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		opacity: 0.9;
		background: $form_bg; } }

.form__title {
	margin-top: 125px;
	margin-bottom: 52px;
	font-size: 2.5em;
	color: #fff;
	text-align: center; }

.form {
	&__inner {
		margin: 15px auto;
		text-align: center; }
	&__item {
		margin: 10px 6px 0 0;
		width: 240px;
		border-radius: 5px;
		background-color: #ffffff;
		padding: 0 15px;
		height: 45px;
		line-height: 45px;
		font-size: 1.125em; }
	&__field {
		text-align: left;
		display: inline-block;
		position: relative;
		width: 240px;
		margin-right: 6px;
		margin-top: 9px; }
	&__legend {
		color: #67ceee;
		font-size: 0.875em;
		font-weight: 400;
		line-height: 24px;
		text-align: left;
		margin-left: 15px; } }

.arrow {
	position: relative;
	&:after {
		content: "";
		position: absolute;
		@include s(arrow_down); } }

@include r(1005) {
	.form__inner {
		margin: 0 auto; }
	.form__item {
		width: 45%; }
	.form__field {
		width: 45%; }
	.form__label {
		display: inline-block;
		width: 45%;
		margin-right: 6px;
		.form__field {
			width: 100%;
			.form__item {
				width: 100%; } } } }

@include r(850) {
	.form__title {
		margin-top: 25px;
		margin-bottom: 25px;
		font-size: 2em; } }


@include r(500) {
	.form__title {
		margin-top: 15px;
		margin-bottom: 10px;
		font-size: 1.5em; }
	.form__item {
		width: 90%; }
	.form__field {
		width: 90%; }
	.form__label {
		display: inline-block;
		width: 90%; }
	.top-form {
		margin-bottom: 10px; } }

@include r(325) {
	.form__title {
		font-size: 1.2em; } }
